.result {
  padding: 50px 165px 60px 150px;
  &__wrapper {
    display: flex;
    justify-content: space-between;
  }
  &__img {
    position: relative;
    z-index: 100;
  }
  &__psevdo_0 {
    position: relative;
    &:before {
      content: 'Больше не нужно брить';
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      color: var(--light-color);
      font-size: 14px;
      top: -277px;
      left: -138px;
      width: 184px;
      height: 184px;
      border-radius: 50%;
      background-color: var(--second-color);
      background-repeat: no-repeat;
    }
  }
  &__wrapper {
    // max-width: 800px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 130px);
    // grid-auto-rows: 200px;
  }
  &__item:first-child {
    grid-column: 1 / 4 span;
  }
  &__item:nth-child(2) {
    grid-column: 6 / 6 span;
    grid-row: 1 / 3 span;
  }
  &__item:nth-child(3) {
    grid-column: 5 / 5 span;
    grid-row: 3 / 3 span;
    img {
      object-fit: contain;
    }
  }
  &__item:nth-child(4) {
    grid-column: 2 / 3 span;
    grid-row: 4 / 5 span;
  }
  &__subtitle {
    font-size: var(--button-size);
    line-height: 25px;
    color: var(--aurum-color);
    padding-top: 10px;
    margin-bottom: 50px;
  }
  &__descr {
    font-size: var(--second-sizes);
    font-family: var(--brands-font);
    line-height: 18px;
    margin-bottom: 10px;
  }
  p {
    font-size: var(--second-sizes);
    font-family: var(--brands-font);
    line-height: 18px;
    padding-left: 10px;
  }
  &__psevdo_1 {
