.contacts {
  background: rgba(217, 217, 217, 0.16);
  .container {
    max-width: 100%;
  }
  &__wrapper {
    position: relative;
  }
  &__info {
    position: absolute;
    width: 280px;
    height: 200px;
    left: 100px;
    top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--bg-color);
  }
  &__addr {
    margin-top: 20px;
    font-size: var(--second-sizes);
    line-height: 19px;
    color: var(--aurum-color);
    text-align: center;
    text-transform: none;
  }
  &__phone {
    margin-top: 15px;
    font-family: var(--second-font);
    font-size: 24px;
    line-height: 19px;
    color: var(--aurum-color);
    &:hover {
      @include hover;
      color: var(--aurum-color);
      transition: all .3s;
    }
  }
  &__social-list {
    margin-top: 25px;
    @include flex-all-center;
  }
  &__social-item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
  &__social-link {
    color: #D1B900;
    transition: color 0.3s;
    &:hover {
      color: var(--main-color);
    }
  }
  &__social-icon {
    width: 35px;
    height: 35px;
    fill: currentColor;
  }
}

.map {
	max-width: 100%;
	height: 500px;
}

[class*="copyrights-pane"] {
	display: none !important;
}

.balloon__address {
	color: var(--aurum-color);
  text-align: center;
}

.balloon__contacts {
  text-align: center;
}

.balloon a {
	color: var(--aurum-color);
}

[class*="balloon__close-button"] {
	opacity: 1;
}

@include tablet {
  .contacts {
    &__info {
      display: none;
    }
  }
}
