.banner {
  padding: 50px 0 60px 140px;
  background-image: url('../img/banner_1.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
  position: relative;
  &__social {
    margin-top: 80px;
  }
  &__social-list {
    display: flex;
    justify-content: flex-start;
  }
  &__social-item {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }
  &__social-link {
    color: var(--second-color);
    transition: color 0.3s;
    &:hover {
      color: var(--main-color);
    }
  }
  &__social-icon {
    width: 110px;
    height: 110px;
    fill: currentColor;
  }
  &__text {
    margin-top: 60px;
    max-width: 315px;
    font-family: var(--brands-font);
    font-size: var(--main-sizes);
    line-height: 16px;
    color: #9EA8A8;
  }
}

@include tablet {
  .banner {
    padding: 50px 0;
  }
}

@include mobile {
  .banner {
    padding: 30px 0;
    &__social {
      margin-top: 145px;
    }
    &__social-list {
      justify-content: space-around;
    }
    &__social-item {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
    &__social-icon {
      width: 60px;
      height: 60px;
      fill: currentColor;
    }
    &__text {
      margin-top: 0;
    }
  }
}

