.title {
  font-family: var(--main-font);
  font-size: var(--title-sizes);
  color: var(--second-color);
  line-height: 61px;
  @include mobile {
    font-size: 32px;
    line-height: 41px;
  }
  &_mw440 {
    max-width: 440px;
  }
  &_aurum {
    color: var(--aurum-color);
    text-align: center;
  }
  &_aurum-left {
    color: var(--aurum-color);
  }
  &_white {
    color: var(--light-color);
    text-align: center;
    @include mobile {
      font-size: 32px;
      line-height: 35px;
    }
  }
  &_fz_40 {
    font-size: var(--subtitle-sizes);
    line-height: 51px;
    color: var(--aurum-color);
  }
  &_mb50 {
    margin-bottom: 50px;
    @include mobile {
      font-size: 32px;
    }
  }
}
