.price {
  padding: 50px 0 10px;
  &__wrapper {
    max-width: 950px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    }
  &__item {
    text-decoration: none;
    min-height: 130px;
    margin-bottom: 30px;
    border: 2px solid var(--second-color);
    border-radius: 3px;
    width: 340px;
    display: flex;
    padding: 20px 20px 16px;
    justify-content: space-between;
  }
  &__item-info {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    }
  &__item-img {
    align-items: center;
    width: 40%;
    }
  &__item-title {
    font-size: var(--second-sizes);
    line-height: 20px;
    margin-bottom: 15px;
  }
  &__item-old {
    font-size: var(--button-size);
    text-align: end;
    color: #B3BABA;
    margin-bottom: 10px;
    padding-right: 10px;
    background-image: url('../img/line_old.svg');
    background-repeat: no-repeat;
    background-size: cover;
    }
  &__item-new {
    font-size: 30px;
    line-height: 17px;
    margin-bottom: 20px;
    color: var(--aurum-color);
  }
  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    // margin-bottom: 50px;
  }
  &__line {
    position: relative;
    width: 50px;
    height: 3px;
    background-color: #08D6C8;
    transform: matrix(0.01, 1, -1, 0, 0, 0);
    &:before {
      content: '';
      position: absolute;
      width: 105px;
      height: 68px;
      top: 46px;
      left: -40px;
      background-image: url('../img/dots.png');
      background-repeat: no-repeat;
      background-size: cover;
      transform: rotate(270deg);
    }
  }
  &__text {
    font-family: var(--brands-font);
    max-width: 320px;
  }
}

@include mobile {
  .price {
    &__item {
      width: 300px;
      padding: 10px 2px 10px;
      justify-content: space-around;
    }
    &__item-title {
      font-size: var(--main-sizes);
    }
    &__item-old {
      font-size: var(--main-sizes);
    }
    &__line {
      &:before {
        display: none;
      }
    }
    &__text {
      max-width: 250px;
    }
  }
}

@include small-tablet {
  .price {
    &__wrapper {
      flex-direction: column;
      align-items: center;
    }
  }
}
