.procedure {
  padding: 50px 165px 60px 150px;
  background-image: url('../img/procedure.png');
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: var(--bg-color);
  .tabs__panel-wrapper {
    max-width: 620px;
  }
  .tabs {
    &__nav {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 80px;
      gap: 80px;
    }
    &__panel-title {
      margin-bottom: 40px;
      text-transform: uppercase;
      font-family: var(--brands-font);
      font-size: var(--second-sizes);
      color: var(--main-color);
      line-height: 18px;
    }
    &__panel-text {
      margin-bottom: 70px;
    }
    p {
      font-family: var(--brands-font);
      font-size: var(--second-sizes);
      color: var(--main-color);
      line-height: 18px;
      &:not(:last-child) {
        margin-bottom: 3px;
      }
    }
    &__nav-btn {
      width: 284px;
      height: 69px;
      background: var(--bg-color);
      border: 1px solid var(--aurum-color);
      border-radius: 60px;
      font-family: var(--second-font);
      font-weight: 400;
      font-size: var(--button-size);
      line-height: 27px;
      color: var(--aurum-color);
      text-align: center;
      padding: 5px 10px;
      cursor: pointer;
      transition: all .3s;
    }
  &__nav-btn--active {
    background: var(--aurum-color);
    color: var(--light-color);
    }
  }
  .tabs__ps1 {
    position: relative;
    &:before {
      content: '1';
      position: absolute;
      width: 102px;
      height: 102px;
      border-radius: 50%;
      background-color: var(--second-color);
      top: -45px;
      left: -31px;
      font-family: var(--main-font);
      font-size: 64px;
      font-weight: 900;
      line-height: 81px;
      color: var(--light-color);
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }
  .tabs__ps2 {
    position: relative;
    &:before {
      content: '2';
      position: absolute;
      width: 102px;
      height: 102px;
      border-radius: 50%;
      background-color: var(--second-color);
      top: -45px;
      left: -65px;
      font-family: var(--main-font);
      font-size: 64px;
      font-weight: 900;
      line-height: 81px;
      color: var(--light-color);
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }
  .tabs__ps3 {
    position: relative;
    &:before {
      content: '3';
      position: absolute;
      width: 102px;
      height: 102px;
      border-radius: 50%;
      background-color: var(--second-color);
      top: -45px;
      left: -60px;
      font-family: var(--main-font);
      font-size: 64px;
      font-weight: 900;
      line-height: 81px;
      color: var(--light-color);
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }
}

@media (max-width: 1200px) {
  .procedure {
    padding: 50px 20px 30px;
  }
}

@include tablet {
  .procedure {
    padding: 50px 0 30px 20px;
    .tabs {
      &__nav {
        flex-direction: column;
        align-items: center;
        gap: 35px;
        margin-bottom: 35px;
      }
      &__nav-btn {
        width: 266px;
        height: 55px;
      }
      &__panel-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      &__panel-title {
        margin-bottom: 20px;
      }
      &__panel-text {
        margin-bottom: 20px;
      }
    }
    .tabs__ps1, .tabs__ps2, .tabs__ps3 {
      &:before {
        width: 83px;
        height: 83px;
        top: -35px;
        left: -60px;
      }
    }
  }
}

@include mobile {
  .procedure {
    padding: 50px 0 30px;
    .tabs {
      &__nav {
        flex-direction: column;
        align-items: center;
        gap: 35px;
        margin-bottom: 35px;
      }
      &__nav-btn {
        width: 266px;
        height: 55px;
      }
      &__panel-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
      &__panel-title {
        margin-bottom: 20px;
      }
      &__panel-text {
        margin-bottom: 20px;
      }
    }
    .tabs__ps1, .tabs__ps2, .tabs__ps3 {
      &:before {
        width: 83px;
        height: 83px;
        top: -35px;
        left: -60px;
      }
    }
  }
}






