.graph-modal {
  &__container {
    max-width: 500px;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__subtitle {
    text-align: center;
    margin-top: 25px;
    margin-bottom: 10px;
    color: var(--aurum-color);
  }
  &__descr {
    margin-bottom: 30px;
    text-align: center;
    color: var(--aurum-color);
  }
  &_mini {
    min-height: 140px;
    transition: 0.3s;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.7);
    cursor: pointer;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    &__container {
      max-width: 300px;
    }
  }
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__label {
    display: flex;
    flex-direction: column-reverse;
    &:not(:first-child) {
      margin-bottom: 20px;
    }
  }
  .just-validate-error-label {
    font-size: 10px;
  }
  &__input {
    border: 3px solid var(--aurum-color);
    border-radius: 4px;
    max-width: 100%;
    padding: 5px 10px;
    margin-bottom: 15px;
    font-family: var(--second-font);
    line-height: 32px;
    &::placeholder {
      color: #DBDBDB;
    }
    &:focus {
      outline: none;
    }
  }
  .placeholder {
    color: #DBDBDB;
  }
}


