.specialists {
  padding: 50px 140px;
  background-color: var(--aurum-color);
  &__wrapper {
    margin-top: 90px;
    max-width: 300px;
  }
  &__img {
    img {
      border-radius: 10px;
    }
  }
  &__name {
    font-size: var(--button-size);
    font-weight: 600;
    color: var(--light-color);
    line-height: 25px;
    margin-top: 30px;
    text-align: center;
  }
  &__descr {
    font-size: var(--button-size);
    color: var(--light-color);
    line-height: 23px;
    margin-top: 10px;
    text-align: center;
  }

    .accordion__wrapper {
        display: flex;
        justify-content: space-around;
        width: 100%;
        padding: 20px;
    }
    .accordion__control {
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style: none;
        padding: 5px 10px;
        margin-bottom: 5px;
        border: 1px solid var(--light-color);
        border-radius: 15px;
        width: 100%;
        background-color: transparent;
        cursor: pointer;
      }
    .accordion__control::-webkit-details-marker {
        display: none;
    }
    .accordion__content {
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      will-change: max-height;
      transition: all 0.3s ease-out;
      box-sizing: content-box;
	    border-top: 1px solid transparent;
    }
    .accordion__item-title {
        font-size:var(--main-sizes);
        color: var(--light-color);
    }
    .accordion__icon {
        position: relative;
        display: inline-block;
        width: 20px;
        height: 20px;
        border-radius: 100%;
        background-color: var(--second-color);
        transition: transform 0.3s ease-out;
        &:before {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            width: 50%;
            height: 1px;
            background-color: var(--light-color);
            transform: translate(-50%, -50%);
        }
    &:after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 1px;
      height: 50%;
      background-color: var(--light-color);
      transform: translate(-50%, -50%);
    }
  }
  .open .accordion__icon {
    transform: rotate(45deg);
  }

  .open .accordion__content {
    opacity: 1;
    padding: 0px;
  }
}

@include tablet {
  .specialists {
    padding: 50px 0 0;
    &__wrapper {
      margin: 0 auto;
      margin-top: 50px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}
