.header {
  margin-bottom: 30px;
  padding-top: 37px;
  &__wrapper {
    @include flex-all-sb;
  }
  .nav {
    &__list {
      @include flex-all-sb;
      margin-left: 177px;
    }
    &__item {
      position: relative;
      font-family: var(--second-font);
      line-height: 19px;
      color: var(--main-color);
      &:not(:last-child) {
        margin-right: 50px;
      }
    }
    &__list--dropdown {
      position: absolute;
      background-color: var(--light-color);
      top: 100%;
      padding: 5px;
      display: block;
      box-shadow: 0 5px 15px 0 rgba(0,0,0, 0.2);
      transition: all 0.3s;
      opacity: 0;
      visibility: hidden;
    }
    .dropdown-submenu {
      position: absolute;
      min-width: 100px;
      min-height: 60px;
      display: flex;
      flex-direction: column;
      align-items: initial;
      margin: 0;
      margin-top: 5px;
      &__item {
        flex-grow: 1;
        min-width: 200px;
        padding: 0 30px;
        &:not(:last-child) {
          border-right: 1px solid #bfbfbf;
        }
        &:first-child {
          padding-left: 0;
        }
        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
  .dropdown-list__item {
    margin-bottom: 5px;
    font-size: var(--second-sizes);
  }

  .dropdown-submenu__list-item {
    margin-bottom: 5px;
  }

  @media (min-width: 769px) {
    .nav__link--drop:hover::after {
      transform: rotate(180deg);
      transition: all 0.3s;
    }

    .nav__item:hover .nav__list--dropdown {
      visibility: visible;
      opacity: 1;
      transition: all 0.3s;
    }
  }
  .social {
    margin-top: 10px;
    @include flex-all-center;
    &__item {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
    &__link {
      color: #D1B900;
      transition: color 0.3s;
      &:hover {
        color: var(--main-color);
      }
    }
    &__icon {
      width: 35px;
      height: 35px;
      fill: currentColor;
    }
  }
  &__social-phone {
    font-family: var(--second-font);
    font-size: 24px;
    line-height: 19px;
    color: var(--main-color);
    &:hover {
      @include hover;
      color: var(--aurum-color);
      transition: all .3s;
    }
  }
}
.burger {
  @include burger;
  display: none;
}
@media (max-width: 1024px) {
  .header {
    padding-top: 10px;
    margin-bottom: 0;
    .burger {
      display: block;
    }
    .nav {
      --header-height: 90px;
      position: fixed;
      right: 0;
      top: var(--header-height);
      z-index: 1000;
      overflow-y: auto;
      padding: 30px 0;
      width: 60%;
      max-width: 300px;
      height: calc(100vh - var(--header-height));
      background-color: var(--bg-color);
      transform: translateX(100%);
      transition: transform 0.6s ease-in-out;
    }
    .nav__list {
      flex-direction: column;
      align-items: start;
      margin-left: 0;
    }
    .nav__link {
      display: inline-block;
      width: 100%;
      padding: 20px 30px;
      border-bottom: 1px solid var(--aurum-color);
      font-size: var(--button-size);
    }
    .nav__link--drop {
      &:after {
        display: none;
      }
    }
    .nav__link--drop:after {
      top: 28px;
    }
    .menu--active {
      transform: none;
    }
    .nav__item {
      width: 100%;
    }

    .nav__item:not(:last-child) {
      margin-right: 0;
    }
  }
}

@media (max-width: 475px) {
  .header {
    &__wrapper {
      padding: 0 15px;
    }
    &__logo {
      width: 90px;
      height: 70px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &__social-phone {
      font-size: 18px;
    }
    .social__icon {
      width: 25px;
      height: 25px;
    }
  }
}

