.review {
  padding: 50px 0;
  background: rgba(217, 217, 217, 0.16);
  &__name {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: var(--second-sizes);
  }
  img {
    max-width: 100%;
    max-height: 100%;
    border-radius: 50%;
    object-fit: contain;
  }
  .swiper1 {
    margin-top: 30px;
    padding-bottom: 50px;
    max-width: 1000px;
    min-height: 560px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    z-index: 1;
  }
  .swiper-wrapper {
    max-width: 100%;
  }
  .slider-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .swiper1 .swiper-slide {
    transition: transform 0.5s;
  }
  .swiper1 .swiper-slide-active {
    transition: transform 0.5s;
  }
  .swiper1 .swiper-pagination .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
    margin: 0 5px;
    background-color: #D9D9D9;
    opacity: 1;
  }
  .swiper1 .swiper-pagination .swiper-pagination-bullet-active {
    background-color: #0ECABD;
  }
  .swiper-button-prev,
  .swiper-button-next {
    background-color: transparent;
    width: 27px;
    height: 72px;
    border-radius: 100%;
    background-position: center;
    background-repeat: no-repeat;
  }
  .swiper-button-prev::after,
  .swiper-button-next::after {
	  display: none;
  }
  .swiper-button-prev {
	  background-image: url("../img/left.png");
  }
  .swiper-button-next {
	  background-image: url("../img/right.png");
  }
}
@media (max-width: 2800px) {
  .swiper-pagination.pag-2 {
    display: none;
  }
}
@media (max-width: 952px) {
  .review {
    .swiper1 {
      max-width: 100%;
    }
    &__text {
      max-width: 350px;
    }
  }
}
@media (max-width: 768px) {
  .review {
    .swiper1 {
      max-width: 100%;
    }
    .swiper-pagination.pag-2 {
      display: block;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    &__text {
      max-width: 350px;
    }
  }
}
@media (max-width: 400px) {
  .review {
    .swiper1 {
      min-height: 510px;
    }
  }
}
