.consultation {
  min-height: 560px;
  padding: 50px 165px 60px 150px;
  background: rgba(217, 217, 217, 0.16);
  background-image: url('../img/consultation.png');
  background-repeat: no-repeat;
  background-position: right;
  &__subtitle {
    font-family: var(--brands-font);
    line-height: 16px;
    margin-top: 90px;
    margin-bottom: 15px;
  }
  &__list {
    margin-bottom: 50px;
  }
  p {
    font-family: var(--brands-font);
    line-height: 16px;
    padding-left: 20px;
  }
}

@include mobile {
  .consultation {
    padding: 40px 0 0 20px;
    position: relative;
    background-image: url('../img/mobile/consultation_mobile.png');
    background-position: right bottom;
    &__subtitle {
      margin-top: 15px;
    }
    p {
      max-width: 255px
    }
    &__inner {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 10px;
      display: flex;
      justify-content: center;
    }
  }
}

@media (max-width: 1025px) {
  .consultation {
    padding: 40px 0 0 20px;
    background-position: center;
  }
}
