.questions {
  padding-top: 90px;
  &__wrapper {
    margin: 100px auto;
    max-width: 930px;
    display: flex;
    justify-content: center;
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
  &__control {
    &:not(:last-child) {
      margin-bottom: 25px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      list-style: none;
      padding: 5px 5px;
      border: none;
      width: 100%;
      background-color: transparent;
      cursor: pointer;
    }
  }
  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--main-color);
  }
  &__icon {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: transparent;
    transition: transform 0.3s ease-out;
    &:before {
        content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 50%;
      height: 4px;
      background-color: var(--second-color);
      transform: translate(-50%, -50%);
    }
    &:after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 4px;
      height: 50%;
      background-color: var(--second-color);
      transform: translate(-50%, -50%);
    }
  }
  .open .questions__icon {
    transform: rotate(45deg);
  }
  .open .questions__content {
    opacity: 1;
    padding: 0px;
  }
  .questions__control::-webkit-details-marker {
    display: none;
  }
  &__content {
    font-size: var(--second-sizes);
    line-height: 19px;
    color: var(--main-color);
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    transition: all 0.3s ease-out;
    box-sizing: content-box;
    border-top: 1px solid transparent;
  }
}

@include mobile {
  .questions {
    padding-top: 40px;
    &__wrapper {
      margin: 30px auto;
    }
    &__item {
      margin-bottom: 10px;
    }
    &__item-title {
      max-width: 255px;
      font-size: var(--second-sizes);
      line-height: 15px;
    }
    &__control {
      &:not(:last-child) {
        margin-bottom: 10px;

      }
    }
  }
}
