.about {
  padding: 50px 0;
  position: relative;
.swiper {
  margin-top: 30px;
  padding-bottom: 50px;
  width: 1000px;
  height: 426px;
}
.swiper-wrapper {
  max-width: 100%;
}
.swiper-slide {
	transition: transform 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 100%;
  opacity: 0.5;
}

.swiper-slide-active {
  scale: 1.1;
  opacity: 1;
	transition: transform 0.5s;
}
img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}
.swiper-pagination .swiper-pagination-bullet {
	width: 15px;
	height: 15px;
	margin: 0 5px;
	background-color: #D9D9D9;
	opacity: 1;
}

.swiper-pagination .swiper-pagination-bullet-active {
	background-color: #0ECABD;
}

.swiper-button-prev,
.swiper-button-next {
  display: none;
  }
}
@media (max-width: 952px) {
  .about {
    .swiper {
      max-width: 100%;
    }
  }
}
@media (max-width: 768px) {
  .about {
    .swiper {
      max-width: 100%;
    }
    .swiper-slide {
      opacity: 1;
    }
    .swiper-slide-active {
      scale: 1;
    }
  }
}
@media (max-width: 639px) {
  .about {
    .swiper {
      padding-bottom: 80px;
    }
  }
}



