.hero {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    width: 655px;
    height: 531px;
    top: 42px;
    right: 100px;
    background: rgba(212, 253, 251, 0.56);
    border-radius: 250px;
    transform: rotate(-46.16deg);
  }
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    background-image: url("../img/banner.png");
    background-repeat: no-repeat;
    background-position: right;
    z-index: 10;
  }
  &__mb13 {
    margin-bottom: 13px;
    max-width: 600px;
  }
  &__mb35 {
    margin-bottom: 35px;
    max-width: 425px;
  }
  &__text {
    font-family: var(--second-font);
    color: var(--main-color);
    font-weight: 400;
    margin-bottom: 25px;
    max-width: 350px;
  }
  &__subtext {
    font-family: var(--second-font);
    color: var(--main-color);
    margin-bottom: 65px;
  }
  &__mb65 {
    margin-bottom: 65px;
  }
  &__litle-text {
    font-family: var(--delius-font);
    font-size: var(--smal-sizes);
    color: #C2C2C2;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
  }
}

@include mobile {
  .hero {
    &:after {
      top: 256px;
    }
    &__wrapper {
      padding: 0 15px;
      align-items: end;
      background-image: url("../img/mobile/hero_mobile.png");
      // transform: rotate(12.57deg);
      background-repeat: no-repeat;
      background-position: left bottom;
    }
    .title {
      max-width: 365px;
      font-weight: 400;
      font-size: 36px;
      line-height: 43px;
      text-align: right;
      &_fz_40 {
        font-weight: 400;
        font-size: 24px;
        line-height: 31px;
        text-align: right;
      }
    }
    &__text {
      text-align: right;
      margin-bottom: 25px;
      max-width: 200px;
    }
    &__subtext {
      text-align: right;
      margin-bottom: 25px;
    }
    &__mb65 {
      margin-bottom: 35px;
    }
    &__inner {
      margin-bottom: 5px;
    }
  }
}

@include small-tablet {
  .hero {
    &__wrapper {
      flex-direction: column;
      align-items: flex-end;
      background-image: url('../img/mobile/hero_mobile.png');
      background-position: left bottom;
    }
    &__mb13 {
      text-align: right;
    }
    &__mb35 {
      text-align: right;
    }
  }
}
