// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "DaysOne-Regular", sans-serif;
  --content-width: 1300px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --main-color: #2C5E5A;
  --second-color: #0ECABD;
  --aurum-color: #D1B900;
  --bg-color: #CBF0EF;

  // font-size
  --main-sizes: 14px;
  --second-sizes: 16px;
  --smal-sizes: 10px;
  --button-size: 20px;
  --title-sizes: 48px;
  --subtitle-sizes: 40px;

    // fonts
    --main-font: "DaysOne-Regular";
    --second-font: "Damion";
    --delius-font: "Delius";
    --brands-font: "Font Awesome 5 Brands";

}
