.button {
  width: 205px;
  height: 57px;
  padding: 15px 45px;
  text-align: center;
  font-family: var(--second-font);
  font-size: var(--button-size);
  line-height: 27px;
  color: var(--light-color);
  background: var(--second-color);
  border: none;
  border-radius: 60px;
  cursor: pointer;
  transition: all .3s;
  &:hover {
    @include hover;
    background: var(--aurum-color);
  }
  &_aurum {
    background: var(--aurum-color);
    border-radius: 60px;
    &:hover {
      @include hover;
      background: var(--second-color);
    }
  }
  &_long {
    width: 404px;
    font-size: var(--button-size);
    color: var(--light-color);
    background: var(--aurum-color);
    &:hover {
      @include hover;
      background: var(--second-color);
    }
    @include mobile {
      width: 266px;
      height: 73px;
      font-size: 16px;
      padding: 5px 45px;
    }
  }
  &_price {
    width: 148px;
    height: 45px;
    padding: 5px 20px;
    background: var(--second-color);
    border: none;
    border-radius: 60px;
  }
}
