.best {
  min-height: 615px;
  padding: 60px 0 0 170px;
  background-image: url('../img/best.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: var(--bg-color);
}

@include tablet {
  .best {
    padding: 40px;
  }
}

@include mobile {
  .best {
    min-height: 355px;
    background-position: center bottom;
  }
}
