.services {
  padding: 50px 165px 60px 150px;
  background-color: var(--bg-color);
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    gap: 10px;
  }
  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  img {
    border-radius: 50%;
  }
  &__descr {
    margin-top: 20px;
  }
  .swiper3 {
    margin-top: 50px;
    max-width: 1100px;
    min-height: 180px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    z-index: 1;
  }
  .swiper-wrapper {
    max-width: 100%;
  }
  .swiper-slide {
    width: auto;
  }
  .services__item {
    min-height: 100px;
    margin-right: 1.5%;
    margin-bottom: 10.5%;
  }
  .swiper-pagination .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
    margin: 0 5px;
    background-color: #D9D9D9;
    opacity: 1;
  }
  .swiper-pagination .swiper-pagination-bullet-active {
    background-color: #0ECABD;
  }
}
  @media (max-width: 2500px) {
    .swiper-pagination .pag-3 {
      display: none;
    }
  }
  @media (max-width: 1250px) {
    .services {
      padding: 50px 0 0;
    }
  }
  @media (max-width: 600px) {
    .services {
      &__wrapper {
        display: flex;
      }
      &__item {
        max-width: 100%;
        margin: 0;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
      }
    }
    .services__btns {
      margin-top: 20px;
    }

    .swiper3 .swiper-wrapper {
      justify-content: initial;
      flex-wrap: nowrap;
    }
    .swiper-pagination {
      display: block;
    }
  }


@include mobile {
  .services {
    padding-top: 40px;
    .title {
      font-weight: 400;
      font-size: 32px;
      line-height: 41px;
      text-align: center;
    }
    &__item {
      margin-bottom: 15.5%;
    }
  }
}



