.benefits {
  position: relative;
  padding-top: 85px;
  background-color: var(--bg-color);
  z-index: 100;
  &__item {
    position: relative;
    max-width: 250px;
    &:before {
      content: '';
      position: absolute;
      width: 126px;
      height: 126px;
      top: -55px;
      left: -60px;
      background-color: var(--light-color);
      border-radius: 50%;
    }
  }
  &__text {
    position: relative;
    display: inline-block;
    max-width: 250px;
    &:before {
      content: '';
      position: absolute;
      width: 58px;
      height: 58px;
      top: -58px;
      left: -76px;
      background-color: var(--second-color);
      border-radius: 50%;
    }
  }
  &__btns {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__item-btn {
    margin-bottom: 30px;
  }
  .swiper2 {
    margin-top: 30px;
    max-width: 1100px;
    min-height: 180px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    z-index: 1;
  }
  .swiper-wrapper {
    margin-top: 30px;
    max-width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .benefits__item {
    width: 100%;
    min-height: 100px;
    margin-right: 1.5%;
    margin-bottom: 2.5%;
    margin-left: 8.5%;
    margin-top: 6.5%;
  }
  .swiper-pagination .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
    margin: 0 5px;
    background-color: #D9D9D9;
    opacity: 1;
  }
  .swiper-pagination .swiper-pagination-bullet-active {
    background-color: #0ECABD;
  }
  @media (max-width: 2500px) {
    .swiper-pagination {
      display: none;
    }
  }
  @media (max-width: 600px) {
    .benefits__item {
      max-width: 100%;
      margin: 0;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
    }
    .benefits__btns {
      margin-top: 20px;
    }

    .swiper2 .swiper-wrapper {
      justify-content: initial;
      flex-wrap: nowrap;
    }
    .swiper-pagination {
      display: block;
    }
  }
}

@include mobile {
  .benefits {
    padding-top: 40px;
    .title {
      font-weight: 400;
      font-size: 32px;
      line-height: 41px;
      text-align: center;
    }
  }
}
